.blogs-wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 20px;
    row-gap: 20px;
  }
  .card-wrapper {
    min-height: 400px;
    height: auto;
    box-sizing: unset;
    border: 1px solid rgba(0, 168, 255, 0.3);
    border-radius: 5px;
    background-color: #fff;
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1);
    cursor: pointer;
  }
  .card-wrapper > img {
    width: 100%;
    height: 176px;
    border-radius: 5px 5px 0 0;
  }
  .blog-details-wrap {
    padding: 0.7em 1.5em;
  }
  .blog-title {
    width: 100%;
    margin: 0;
    font-size: 20px;
    font-weight: 600;
    line-height: 1.14;
    color: #000;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }
  .blog-author {
    font-size: 0.9em;
    font-weight: 500;
    line-height: 2;
    color: #0096ff;
    margin: 0;
    padding-top: 4px;
  }
  .post-date {
    font-size: 14px;
    font-family: 'Roboto';
    font-weight: 300;
    color: #000;
    margin-top: 0.3em;
  }
  .description {
    width: 100%;
    opacity: 0.5;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.43;
    letter-spacing: 0.35px;
    color: #000;
    margin: 0.3em 0;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 5;
    overflow: hidden;
  }
  