@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
*{
    box-sizing: border-box;
    margin: 0;
}

body {
  margin: 0;
  width: 100%;
  min-height: 100vh;
  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f2f6ff;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.stories-sec{
  width: 100%;
  box-sizing: border-box;
}
.topbar{
    width: 100%;
    height: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 1em 4.8em;
    background: #fff;
    box-shadow: 0 1px 1px #ccc;
    position: fixed;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    z-index: 999;
}

.logo-wrapper{
    text-decoration: none;
}

.logo{
    color: #0048ff;
    background-image: linear-gradient(296deg, #0048ff, #0096ff), linear-gradient(#000, #000);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
    text-transform: uppercase;
    font-size: 2.1rem ;
    font-weight: 900;
    letter-spacing: -0.9px;
}

.sub-logo{
    font-size: 1rem;
    color: #000;
    font-style: italic;
    font-weight: 500;
    line-height: 0.8;
   letter-spacing: 0.9px;
}

nav{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 80%;
    -webkit-align-items: center;
            align-items: center;
}

.explore-wrapper{
    position: relative;
    height: 100%;
}

.explore-sec{
    font-weight: 500;
    cursor: pointer;
    font-size: 14px;
    font-family: 'Open Sans', sans-serif;
    position: relative;
    padding: 10px 0;
    margin-top: -20px;
}

.explore-sec i{
    font-size: 1.2rem;
    margin-left: 10px;
    margin-top: 20px;
    font-weight: 600;
    font-stretch: normal;
}

.categories{
    min-width: 250px;
    font-size: 16px;
    line-height: 1em;
    padding: .5em;
     background: #fff;
     position: absolute;
     top: 69px;
     left: 10%;
     display: -webkit-flex;
     display: flex;
     -webkit-flex-direction: column;
             flex-direction: column;
     border-radius: 4px;
    box-shadow: 0 1px 8px rgb(0 0 0 / 50%);
    padding: .5em 1em;
}

.categories>i::after{
    content: "";
    position: absolute;
    width: 12px;
    height: 12px;
    top: -13px;
    left: 50%;
    -webkit-transform: translate(-50%,50%) rotate(45deg);
    transform: translate(-50%,50%) rotate(45deg);
    background-color: #fff;
    box-shadow: -1px -1px 8px rgb(0 0 0 / 50%);
    z-index: -1;
}

.categories>a{
    width: 100%;
  text-decoration: none;
  display: block;
  padding: 8px 16px;
  color: rgba(0, 0, 0, 0.7);
  font-weight: 400;
  font-size: 14px;
  font-family: 'Raleway', sans-serif;
  cursor: pointer;
  letter-spacing: 0.9px;
}

.nav-right{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    width: 55%;
}

.site-quote{
    min-width: 23em;
  opacity: 0.6;
  font-family: 'Raleway', sans-serif;
  font-size: 15px;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0.4px;
  text-align: left;
  color: rgba(0, 0, 0, 0.6);
}

.main-web-redirect{
    background: linear-gradient(269deg, #0048ff, #0096ff);
  border: none;
  border-radius: 25px;
  box-shadow: none;
  color: #fff;
  cursor: pointer;
  font-family: 'Raleway', sans-serif;
  font-size: 0.75em;
  font-weight: 600;
  height: 42px;
  margin: 0px 1em 0px 0px;
  min-width: 136px;
  outline: none;
  padding: 0.5em 2em;
  width: auto;
}
.blogs-wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;
    grid-row-gap: 20px;
    row-gap: 20px;
  }
  .card-wrapper {
    min-height: 400px;
    height: auto;
    box-sizing: unset;
    border: 1px solid rgba(0, 168, 255, 0.3);
    border-radius: 5px;
    background-color: #fff;
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1);
    cursor: pointer;
  }
  .card-wrapper > img {
    width: 100%;
    height: 176px;
    border-radius: 5px 5px 0 0;
  }
  .blog-details-wrap {
    padding: 0.7em 1.5em;
  }
  .blog-title {
    width: 100%;
    margin: 0;
    font-size: 20px;
    font-weight: 600;
    line-height: 1.14;
    color: #000;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }
  .blog-author {
    font-size: 0.9em;
    font-weight: 500;
    line-height: 2;
    color: #0096ff;
    margin: 0;
    padding-top: 4px;
  }
  .post-date {
    font-size: 14px;
    font-family: 'Roboto';
    font-weight: 300;
    color: #000;
    margin-top: 0.3em;
  }
  .description {
    width: 100%;
    opacity: 0.5;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.43;
    letter-spacing: 0.35px;
    color: #000;
    margin: 0.3em 0;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 5;
    overflow: hidden;
  }
  
.filterSection {
    margin: 16px 0;
  }

  .filter-title-wrap {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 1rem;
  }

  .filter-icon {
    width: 30px;
    height: 25px;
  }

  .filter-icon > img {
    width: 100%;
    height: 100%;
  }

  .filter-title {
    font-size: 0.85em;
    font-weight: 600;
    margin-left: 0.5em;   
  }

  .filters-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin-bottom: 1.5rem;
  }

  .filter-option {
    padding: 6px 14px;
    width: auto;
    font-size: 12px;
    color: #0084ff;
    border-radius: 15px;
    border: 1px solid #0084ff;
    margin: 5px;
    cursor: pointer;
  }

  .active{
    background: linear-gradient(269deg, #0048ff, #0096ff);
    color: #fff;
  }
  
.content-wrapper{
    padding-top: 120px;
    width: 70%;
    margin: 0 auto;
}

.posts-title{
    margin-bottom: 1rem;
}
